import React, { Component} from 'react';
import strings from "../../Localisation";
import VideoFunctional from "../../base/video/VideoFunctional";
import Space from "../../base/functional/Space";
import {FACEL_HEALTH__TIREDNESS_VIDEO, FACEL_HEALTH__TIREDNESS_HISTORY_VIDEO} from "../../../utils/Videos";
import PhotoFunctional from "../../base/photo/PhotoFunctional";

export default class Health extends Component {

    static getHealthArray() {
        return [
            FACEL_HEALTH__TIREDNESS_VIDEO,
            FACEL_HEALTH__TIREDNESS_HISTORY_VIDEO
        ]
    }

    static getHealthPhotosArray() {
        return [
            "/images/main/shots/health/advices.png",
            "/images/main/shots/health/fatigue.png",
            "/images/main/shots/health/today.png"
        ]
    }

    render() {
        return (
           <div>
               <Space />
               <PhotoFunctional
                   array={Health.getHealthPhotosArray()}
                   title={strings.health__title}
                   description={strings.health__description} />

           </div>
        )
    }
}