import React, { Component} from 'react';
import strings from "../../Localisation";
import VideoFunctional from "../../base/video/VideoFunctional";
import Space from "../../base/functional/Space";
import {FACEL_HEALTH__ORGANISATIONS_VIDEO, FACEL_HEALTH__TIREDNESS_HISTORY_VIDEO} from "../../../utils/Videos";
import PhotoFunctional from "../../base/photo/PhotoFunctional";

export class Business extends Component {

    static getBusinessArray() {
        return [
            FACEL_HEALTH__ORGANISATIONS_VIDEO,
            FACEL_HEALTH__TIREDNESS_HISTORY_VIDEO
        ]
    }

    static getPhotos() {
        return [
            "/images/main/shots/admin/stress.png",
            "/images/main/shots/admin/high.png",
            "/images/main/shots/admin/history.png",
            "/images/main/shots/admin/details.png"
        ]
    }

    render() {
        return (
            <div>
                <Space />
                <PhotoFunctional
                    array={Business.getPhotos()}
                    title={strings.business__title}
                    description={strings.business__description} />
            </div>
        )
    }
}