import React, { Component} from 'react';
import Header from "../main/header/Header";
import strings from "../Localisation";
import Title from "../base/functional/Title";
import Text from "../base/functional/Text";
import Space from "../base/functional/Space";
import VideoFunctional from "../base/video/VideoFunctional";
import {SpeedMeasure} from "../../utils/SpeedMeasure";
import Footer from "../main/footer/Footer";
import Preorder from "../main/preorder/Preorder";
import {
    FACEL__COMPUTER_CONNECTION_VIDEO,
    FACEL__CREATED_ACTIONS_VIDEO,
    FACEL__CREATING_ACTION_VIDEO,
    FACEL__DIAGNOSTICS_VIDEO,
    FACEL__RETRAINING_VIDEO
} from "../../utils/Videos";
import Subtitle from "../base/functional/Subtitle";
import {LanguagesUtils} from "../../utils/LanguagesUtils";

export class Facel extends Component {
    static getControlArray() {
        return [
            FACEL__CREATING_ACTION_VIDEO,
            FACEL__CREATED_ACTIONS_VIDEO,
        ]
    }

    render() {
        let quality = SpeedMeasure.getResolution([
            Facel.getControlArray()
        ]);

        let languages = new LanguagesUtils();
        strings.setLanguage(languages.getCurrentLanguage())

        return (
            <div className="app">
                <Header />

                <Space />

                <div className={"fullpage_text"}>
                    <Title title={strings.control__title} />
                    <Text text={strings.control__description} />
                </div>

                <Space />

                <VideoFunctional
                    quality={quality}
                    array={Facel.getControlArray()}
                    title={strings.facel__actions__title}
                    description={strings.facel__actions__description} />

                <Space />

                <VideoFunctional
                    quality={quality}
                    array={[FACEL__COMPUTER_CONNECTION_VIDEO]}
                    title={strings.facel__computer_connection__title}
                    description={strings.facel__computer_connection__description} />

                <Space />

                <VideoFunctional
                    quality={quality}
                    array={[FACEL__RETRAINING_VIDEO]}
                    title={strings.facel__retraining__title}
                    description={strings.facel__retraining__description} />

                <Space />

                <VideoFunctional
                    quality={quality}
                    array={[FACEL__DIAGNOSTICS_VIDEO]}
                    title={strings.facel__diagnostics__title}
                    description={strings.facel__diagnostics__description} />

                <Space />

                <Preorder />
                <Space />
                <Footer />
            </div>
        )
    }
}