import React, { Component} from 'react';
import Header from "../main/header/Header";
import strings from "../Localisation";
import Title from "../base/functional/Title";
import Text from "../base/functional/Text";
import Space from "../base/functional/Space";
import VideoFunctional from "../base/video/VideoFunctional";
import {SpeedMeasure} from "../../utils/SpeedMeasure";
import Footer from "../main/footer/Footer";
import Preorder from "../main/preorder/Preorder";
import {FACEL_HEALTH__ORGANISATIONS_VIDEO, FACEL_HEALTH__TIREDNESS_VIDEO, FACEL_HEALTH__TIREDNESS_HISTORY_VIDEO} from "../../utils/Videos";
import {Spa} from "mathjs";
import {LanguagesUtils} from "../../utils/LanguagesUtils";

export class Rehealth extends Component {
    static getControlArray() {
        return [
            FACEL_HEALTH__TIREDNESS_HISTORY_VIDEO,
            FACEL_HEALTH__ORGANISATIONS_VIDEO,
            FACEL_HEALTH__TIREDNESS_VIDEO
        ]
    }

    render() {
        let languages = new LanguagesUtils();
        strings.setLanguage(languages.getCurrentLanguage())

        let quality = SpeedMeasure.getResolution([
            Rehealth.getControlArray()
        ]);

        return (
            <div className="app">
                <Header />
                <Space />

                <div className={"fullpage_text"}>
                    <Title title={strings.health__title} />
                    <Text text={strings.health__description} />
                </div>

                <Space />

                <VideoFunctional
                    quality={quality}
                    array={[FACEL_HEALTH__TIREDNESS_VIDEO]}
                    title={strings.business__tiredness__title}
                    description={strings.business__tiredness__description} />

                <Space />

                <VideoFunctional
                    quality={quality}
                    array={[FACEL_HEALTH__TIREDNESS_HISTORY_VIDEO]}
                    title={strings.business__tiredness_history__title}
                    description={strings.business__tiredness_history__description} />

                <Space />

                <VideoFunctional
                    quality={quality}
                    array={[FACEL_HEALTH__ORGANISATIONS_VIDEO]}
                    title={strings.business__organisations__title}
                    description={strings.business__organisations__description} />

                <Preorder />
                <Space />
                <Footer />
            </div>
        )
    }
}