import React, { Component} from 'react';
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import strings from "../Localisation";
import {SpeedMeasure} from "../../utils/SpeedMeasure";
import Space from "../base/functional/Space";
import {StepFunctional} from "../base/steps/StepFunctional";
import {FACEL__AVAILABLE_GESTURES_VIDEO} from "../../utils/Videos";
import {LanguagesUtils} from "../../utils/LanguagesUtils";

export class Characteristics extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    static getCharacteristicsArray() {
        return [
            FACEL__AVAILABLE_GESTURES_VIDEO
        ]
    }

    render() {
        let quality = SpeedMeasure.getResolution([
            Characteristics.getCharacteristicsArray()
        ]);

        let languages = new LanguagesUtils();
        strings.setLanguage(languages.getCurrentLanguage())

        return (
            <div className={"app"}>
                <Header />
                <Space />

                <StepFunctional
                    array={Characteristics.getCharacteristicsArray()}
                    quality={quality}
                    steps={[
                        strings.characteristics__first,
                        strings.characteristics__second,
                        strings.characteristics__third,
                        strings.characteristics__fourth,
                        strings.characteristics__fifth,
                        strings.characteristics__sixth,
                        strings.characteristics__seventh,
                        strings.characteristics__eighth
                    ]}
                    steppersEnabled={false}
                    title={strings.characteristics__title}
                    description={strings.characteristics__description} />

                <Space />
                <Footer />
            </div>
        )
    }
}