import React, {Component} from "react";
import strings from "../Localisation";
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import {SpeedMeasure} from "../../utils/SpeedMeasure";
import VideoFunctional from "../base/video/VideoFunctional";
import Space from "../base/functional/Space";
import {StepFunctional} from "../base/steps/StepFunctional";
import {
    FACEL_HEALTH__TIREDNESS_VIDEO,
    FACEL_HEALTH__TIREDNESS_HISTORY_VIDEO,
    FACEL__COMPUTER_CONNECTION_VIDEO,
    FACEL__CREATING_ACTION_VIDEO
} from "../../utils/Videos";
import {LanguagesUtils} from "../../utils/LanguagesUtils";

export class Instructions extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    getControllingPhoneArray() {
        return [
            FACEL__CREATING_ACTION_VIDEO
        ]
    }

    getControllingComputerArray() {
        return [
            FACEL__COMPUTER_CONNECTION_VIDEO
        ]
    }

    getEyesHealthArray() {
        return [
            FACEL_HEALTH__TIREDNESS_VIDEO,
            FACEL_HEALTH__TIREDNESS_HISTORY_VIDEO
        ]
    }

    render() {
        let quality = SpeedMeasure.getResolution([
            this.getControllingComputerArray(),
            this.getControllingPhoneArray(),
            this.getEyesHealthArray()
        ]);

        let languages = new LanguagesUtils();
        strings.setLanguage(languages.getCurrentLanguage())

        console.log("quality: " + quality);

        return (
            <div className={"app"}>
                <Header />
                <Space />

                <StepFunctional
                    steppersEnabled={true}
                    array={this.getControllingPhoneArray()}
                    quality={quality}
                    title={strings.instructions__controlling_phone__title}
                    steps={[
                        strings.instructions__controlling_phone__first,
                        strings.instructions__controlling_phone__second,
                        strings.instructions__controlling_phone__third
                    ]} />

                <Space />

                <StepFunctional
                    steppersEnabled={true}
                    array={this.getControllingComputerArray()}
                    quality={quality}
                    title={strings.instructions__controlling_computer__title}
                    steps={[
                        strings.instructions__controlling_computer__first,
                        strings.instructions__controlling_computer__second
                    ]} />

                <Space />

                <StepFunctional
                    steppersEnabled={true}
                    array={this.getEyesHealthArray()}
                    quality={quality}
                    title={strings.instructions__keeping_eyes_health__title}
                    steps={[
                        strings.instructions__keeping_eyes_health__first,
                        strings.instructions__keeping_eyes_health__second
                    ]} />

                <Space />
                <Footer />
            </div>
        )
    }
}