import React, { Component} from 'react';
import strings from "../../Localisation";
import VideoFunctional from "../../base/video/VideoFunctional";
import Space from "../../base/functional/Space";
import {
    FACEL__CREATED_ACTIONS_VIDEO,
    FACEL__CREATING_ACTION_VIDEO
} from "../../../utils/Videos";
import PhotoFunctional from "../../base/photo/PhotoFunctional";

export default class Control extends Component {
    static getControlArray() {
        return [
            FACEL__CREATING_ACTION_VIDEO,
            FACEL__CREATED_ACTIONS_VIDEO,
        ]
    }

    static getPhotos() {
        return [
            "/images/main/shots/facel/actions.png",
            "/images/main/shots/facel/history.png",
            "/images/main/shots/facel/accum.png",
            "/images/main/shots/facel/network.png"
        ]
    }

    render() {
        return (
           <div>
               <Space />
               <PhotoFunctional
                   array={Control.getPhotos()}
                   title={strings.control__title}
                   description={strings.control__description} />
           </div>
        )
    }
}